import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../../public/services/auth.service';
import { MainService } from '../../../public/services/main.service';

@Component({
  selector: 'ngx-charts-index',
  templateUrl: './charts-index.component.html',
  styleUrls: ['./charts-index.component.scss'],
})
export class ChartsIndexComponent implements OnInit {
  /** API URL */
  readonly API_DEMOGRAPHICS = 'api/user/demographics';

  loading: boolean = true;

  // stats
  kidsConfig: any;
  petConfig: any;
  genderConfig: any;
  educationConfig: any;
  stratumConfig: any;
  maritalStatusConfig: any;

  // total users
  total: 0;

  // options
  showXAxis: boolean = true;
  showYAxis: boolean = true;
  gradient: boolean = false;
  showLegend: boolean = true;
  showXAxisLabel: boolean = true;
  yAxisLabel: string = 'Característica proporcionada';
  showYAxisLabel: boolean = true;
  xAxisLabel: string = 'Total usuarios %';
  legendTitle: string = '';
  roundDomains: true;
  colorScheme = {
    domain: ['#3d5a80', '#98c1d9', '#e0fbfc', '#ee6c4d', '#293241', '#f7b2ad', '#b89685'],
  };

  // % data per stat
  multi: any;

  constructor(private mainService: MainService, private authService: AuthService) {}

  ngOnInit(): void {
    this.fetchDemographics();
  }

  fetchDemographics() {
    this.mainService
      .get({ api: this.API_DEMOGRAPHICS })
      .then((value) => {
        this.populateTables(value);
      })
      .finally(() => {
        this.loading = false;
      });
  }

  getPercentValue(value) {
    if (!value) {
      return 0;
    }
    return Math.round((100 * value) / this.total);
  }

  populateTables(data) {
    this.total = data.totalUsuarios;
    this.kidsConfig = {
      data: [
        {
          name: `Sí: ${this.getPercentValue(data.hijos.true)}%`,
          value: data.hijos.true ? data.hijos.true : 0,
        },
        {
          name: `No: ${this.getPercentValue(data.hijos.false)}%`,
          value: data.hijos.false ? data.hijos.false : 0,
        },
        {
          name: `No registra: ${this.getPercentValue(data.hijos.noRegistra)}%`,
          value: data.hijos.noRegistra ? data.hijos.noRegistra : 0,
        },
      ],

      showXAxis: true,
      showYAxis: true,
      gradient: false,
      showLegend: false,
      showXAxisLabel: true,
      xAxisLabel: '¿Tiene hijos?',
      showYAxisLabel: true,
      yAxisLabel: 'Cantidad de usuarios',
      animations: true,
      legendTitle: '',
      colorScheme: this.colorScheme,
    };

    this.petConfig = {
      data: [
        {
          name: `Sí: ${this.getPercentValue(data.mascotas.true)}%`,
          value: data.mascotas.true ? data.mascotas.true : 0,
        },
        {
          name: `No: ${this.getPercentValue(data.mascotas.false)}%`,
          value: data.mascotas.false ? data.mascotas.false : 0,
        },
        {
          name: `No registra: ${this.getPercentValue(data.mascotas.noRegistra)}%`,
          value: data.mascotas.noRegistra ? data.mascotas.noRegistra : 0,
        },
      ],

      showXAxis: true,
      showYAxis: true,
      gradient: false,
      showLegend: false,
      showXAxisLabel: true,
      xAxisLabel: '¿Tiene mascotas?',
      showYAxisLabel: true,
      yAxisLabel: 'Cantidad',
      animations: true,
      legendTitle: '',
      colorScheme: this.colorScheme,
    };

    this.genderConfig = {
      data: [
        {
          name: `Masculino: ${this.getPercentValue(data.genero.Masculino)}%`,
          value: data.genero.Masculino ? data.genero.Masculino : 0,
        },
        {
          name: `Femenino: ${this.getPercentValue(data.genero.Femenino)}%`,
          value: data.genero.Femenino ? data.genero.Femenino : 0,
        },
        {
          name: `Otro: ${this.getPercentValue(data.genero.Otro)}%`,
          value: data.genero.Otro ? data.genero.Otro : 0,
        },
        {
          name: `Prefiero no decir: ${this.getPercentValue(data.genero.PrefieroNoDecir)}%`,
          value: data.genero.PrefieroNoDecir ? data.genero.PrefieroNoDecir : 0,
        },
        {
          name: `No registra: ${this.getPercentValue(data.genero.noRegistra)}%`,
          value: data.genero.noRegistra ? data.genero.noRegistra : 0,
        },
      ],

      showXAxis: true,
      showYAxis: true,
      gradient: false,
      showLegend: false,
      showXAxisLabel: true,
      xAxisLabel: 'Géneros',
      showYAxisLabel: true,
      yAxisLabel: 'Cantidad',
      legendTitle: '',
      animations: true,

      colorScheme: this.colorScheme,
    };

    this.educationConfig = {
      data: [
        {
          name: `Primaria: ${this.getPercentValue(data.nivelEducativo.Primaria)}%`,
          value: data.nivelEducativo.Primaria ? data.nivelEducativo.Primaria : 0,
        },
        {
          name: `Bachillerato: ${this.getPercentValue(data.nivelEducativo.Bachillerato)}%`,
          value: data.nivelEducativo.Bachillerato ? data.nivelEducativo.Bachillerato : 0,
        },
        {
          name: `Pregrado: ${this.getPercentValue(data.nivelEducativo.Pregrado)}%`,
          value: data.nivelEducativo.Pregrado ? data.nivelEducativo.Pregrado : 0,
        },
        {
          name: `Doctorado: ${this.getPercentValue(data.nivelEducativo.Doctorado)}%`,
          value: data.nivelEducativo.Doctorado ? data.nivelEducativo.Doctorado : 0,
        },
        {
          name: `No registra: ${this.getPercentValue(data.nivelEducativo.noRegistra)}%`,
          value: data.nivelEducativo.noRegistra ? data.nivelEducativo.noRegistra : 0,
        },
      ],

      showLegend: true,
      showLabels: true,
      isDoughnut: false,
      trimLabels: false,
      legendTitle: '',
      legendPosition: 'right',
      colorScheme: this.colorScheme,
    };

    this.stratumConfig = {
      data: [
        {
          name: `Estrato 1: ${this.getPercentValue(data.estrato['1'])}`,
          value: data.estrato['1'] ? data.estrato['1'] : 0,
        },
        {
          name: `Estrato 2: ${this.getPercentValue(data.estrato['2'])}%`,
          value: data.estrato['2'] ? data.estrato['2'] : 0,
        },
        {
          name: `Estrato 3: ${this.getPercentValue(data.estrato['3'])}%`,
          value: data.estrato['3'] ? data.estrato['3'] : 0,
        },
        {
          name: `Estrato 4: ${this.getPercentValue(data.estrato['4'])}%`,
          value: data.estrato['4'] ? data.estrato['4'] : 0,
        },
        {
          name: `Estrato 5: ${this.getPercentValue(data.estrato['5'])}%`,
          value: data.estrato['5'] ? data.estrato['5'] : 0,
        },
        {
          name: `Estrato 6: ${this.getPercentValue(data.estrato['6'])}%`,
          value: data.estrato['6'] ? data.estrato['6'] : 0,
        },
        {
          name: `No registra: ${this.getPercentValue(data.estrato.noRegistra)}%`,
          value: data.estrato.noRegistra ? data.estrato.noRegistra : 0,
        },
      ],

      showLegend: false,
      showLabels: true,
      trimLabels: false,
      legendTitle: '',
      isDoughnut: false,
      legendPosition: 'right',
      colorScheme: this.colorScheme,
    };

    this.maritalStatusConfig = {
      data: [
        {
          name: `Soltero/a: ${this.getPercentValue(data.estadoCivil.Soltero)}%`,
          value: data.estadoCivil.Soltero ? data.estadoCivil.Soltero : 0,
        },
        {
          name: `Casado/a: ${this.getPercentValue(data.estadoCivil.Casado)}%`,
          value: data.estadoCivil.Casado ? data.estadoCivil.Casado : 0,
        },
        {
          name: `Viuda/a: ${this.getPercentValue(data.estadoCivil.Viudo)}%`,
          value: data.estadoCivil.Viudo ? data.estadoCivil.Viudo : 0,
        },
        {
          name: `Divorciado/a: ${this.getPercentValue(data.estadoCivil.Divorciado)}%`,
          value: data.estadoCivil.Divorciado ? data.estadoCivil.Divorciado : 0,
        },
        {
          name: `No registra: ${this.getPercentValue(data.estadoCivil.noRegistra)}%`,
          value: data.estadoCivil.noRegistra ? data.estadoCivil.noRegistra : 0,
        },
      ],

      showLegend: true,
      showLabels: true,
      isDoughnut: false,
      legendTitle: '',
      legendPosition: 'right',
      colorScheme: this.colorScheme,
    };

    this.multi = [
      {
        name: 'Género',
        series: [
          {
            name: 'Registra',
            value: data.totalUsuarios - (data.genero.noRegistra ? data.genero.noRegistra : 0),
          },
          {
            name: 'No registra',
            value: data.genero.noRegistra ? data.genero.noRegistra : 0,
          },
        ],
      },

      {
        name: 'Estrato',
        series: [
          {
            name: 'Registra',
            value: data.totalUsuarios - (data.estrato.noRegistra ? data.estrato.noRegistra : 0),
          },
          {
            name: 'No registra',
            value: data.estrato.noRegistra ? data.estrato.noRegistra : 0,
          },
        ],
      },
      {
        name: 'Nivel educativo',
        series: [
          {
            name: 'Registra',
            value: data.totalUsuarios - (data.nivelEducativo.noRegistra ? data.nivelEducativo.noRegistra : 0),
          },
          {
            name: 'No registra',
            value: data.nivelEducativo.noRegistra ? data.nivelEducativo.noRegistra : 0,
          },
        ],
      },
      {
        name: 'Hijos',
        series: [
          {
            name: 'Registra',
            value: data.totalUsuarios - (data.hijos.noRegistra ? data.hijos.noRegistra : 0),
          },
          {
            name: 'No registra',
            value: data.hijos.noRegistra ? data.hijos.noRegistra : 0,
          },
        ],
      },

      {
        name: 'Mascotas',
        series: [
          {
            name: 'Registra',
            value: data.totalUsuarios - (data.mascotas.noRegistra ? data.mascotas.noRegistra : 0),
          },
          {
            name: 'No registra',
            value: data.mascotas.noRegistra ? data.mascotas.noRegistra : 0,
          },
        ],
      },
      {
        name: 'Estado civil',
        series: [
          {
            name: 'Registra',
            value: data.totalUsuarios - (data.estadoCivil.noRegistra ? data.estadoCivil.noRegistra : 0),
          },
          {
            name: 'No registra',
            value: data.estadoCivil.noRegistra ? data.estadoCivil.noRegistra : 0,
          },
        ],
      },
    ];
  }
}
